@charset "UTF-8";
body {
  font-family: "Cera Pro";
  margin: 0;
  padding: 0;
}

.whole-page {
  background: linear-gradient(228.57deg, #323264 11.93%, #323264 57.17%, #643E72 87.46%);
}

.main-page {
  background-image: url("../images/Symbol.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 100vh;
  width: 100%;
}

.first-block {
  padding: 50px 160px;
}
.first-block_header {
  display: flex;
  justify-content: space-between;
}
.first-block_header_right-menu {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.first-block_header_right-menu_upper {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 16px;
}
.first-block_header_right-menu_upper_language {
  display: flex;
  align-items: center;
}
.first-block_header_right-menu_upper_language_text {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 33px;
  font-size: 12px;
  font-weight: 700;
  padding: 5px 7px;
  line-height: 14.63px;
  color: #FFFFFF;
  text-decoration: none;
  border-radius: 8px;
  transition: all 0.2s ease;
}
.first-block_header_right-menu_upper_language_text_active {
  font-size: 12px;
  font-weight: 700;
  line-height: 14.63px;
  color: #FFFFFF;
  text-decoration: none;
  background: #F0145A;
  padding: 5px 7px;
  border-radius: 8px;
}
.first-block_header_right-menu_upper_dot {
  width: 6px;
  height: 6px;
  background: #E6E6F0;
  border-radius: 50%;
}
.first-block_header_right-menu_upper_text {
  font-size: 16px;
  font-weight: 400;
  line-height: 19.5px;
  color: #FFFFFF;
}
.first-block_header_right-menu_upper_number {
  font-size: 16px;
  font-weight: 700;
  line-height: 19.5px;
  color: #FFFFFF;
}
.first-block_header_right-menu_upper_facebook-svg {
  color: #FFFFFF;
}
.first-block_header_right-menu_lower {
  display: flex;
  align-items: center;
  gap: 15px;
}
.first-block_header_right-menu_lower_text {
  font-size: 16px;
  font-weight: 700;
  line-height: 19.5px;
  color: #FFFFFF;
  text-decoration: none;
  border-bottom: 3px solid transparent;
  transition: all 0.2s ease;
}
.first-block_header_right-menu_lower_text_active {
  padding-bottom: 3px;
  color: #F0145A;
  border-bottom: 2px solid;
  text-decoration: none;
}

.first-block_header_right-menu_upper_facebook-svg:hover {
  color: #F0145A;
  transition: all 0.5s ease;
}

.first-block_header_right-menu_upper_language_text:hover {
  background-color: rgba(255, 255, 255, 0.1254901961);
}

.second-header {
  display: none;
}

.third-header {
  display: none;
}

.first-block_center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.first-block_center_text {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #FFFFFF;
  font-size: 38px;
  font-weight: 700;
  line-height: 46.32px;
}

.first-block_down-arrow {
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
}

.second {
  display: flex;
  justify-content: center;
  align-items: center;
}
.second_block {
  display: flex;
  align-items: center;
  justify-content: center;
  justify-content: space-between;
  gap: 60px;
  padding: 60px 0;
}
.second_block_left-menu {
  display: flex;
  flex-direction: column;
  align-items: end;
  gap: 20px;
  min-width: fit-content;
}
.second_block_left-menu_text_active {
  font-size: 38px;
  font-weight: 700;
  line-height: 46.32px;
  text-decoration: none;
  justify-content: end;
  background: linear-gradient(90.13deg, #EF8080 0.84%, #F0145A 99.96%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.second_block_left-menu_text {
  font-size: 38px;
  font-weight: 700;
  line-height: 46.32px;
  color: #B3B3BA;
  text-decoration: none;
}
.second_block_verical-stick {
  width: 4px;
  height: 303px;
  background: rgba(255, 255, 255, 0.3019607843);
}
.second_block_right-menu {
  display: flex;
  max-width: fit-content;
  max-height: 157px;
  flex-direction: column;
  max-width: 861px;
  height: 100%;
  padding: 43px 0;
}
.second_block_right-menu_upper-text {
  max-width: 861px;
  font-size: 30px;
  font-weight: 700;
  line-height: 36.57px;
  color: #FFFFFF;
  padding-bottom: 30px;
}
.second_block_right-menu_lower-text {
  max-width: 861px;
  font-size: 19px;
  font-weight: 400;
  line-height: 30px;
  color: #FFFFFF;
}

.third-block_height-text {
  padding-left: 60px;
}
.third-block_height-text_block {
  display: flex;
  align-items: center;
  gap: 2px;
}
.third-block_height-text_block_up {
  font-size: 14px;
  font-weight: 700;
  line-height: 17.07px;
  color: #24A3FF;
}
.third-block_height-text_block_low {
  font-size: 38px;
  font-weight: 700;
  line-height: 46.32px;
  background: -webkit-linear-gradient(90.13deg, #F0145A 0.84%, #EF8080 99.96%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.third-block_card-container {
  min-width: 100%;
  display: grid;
  grid-template: auto/auto auto auto;
  gap: 40px 60px;
}
.third-block_card-container_card {
  width: fit-content;
  min-height: 268px;
}
.third-block_card-container_card_img {
  padding-bottom: 30px;
}
.third-block_card-container_card_text-up {
  font-size: 22px;
  font-weight: 700;
  line-height: 26.82px;
  color: #24A3FF;
  padding-bottom: 15px;
}
.third-block_card-container_card_text-low {
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
  color: #FFFFFF;
}

.third-block_card-container-block {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-template: repeat(auto-fill, minmax(200px, 1fr));
  gap: 20px;
  padding: 60px;
}

.fourth {
  display: flex;
  justify-content: center;
  padding: 60px 60px;
}
.fourth_block {
  display: flex;
  max-width: 1600px;
  justify-content: center;
  padding-bottom: 46px;
}
.fourth_block_img {
  max-width: 400px;
  max-height: 100%;
  -o-object-fit: cover;
  object-fit: cover;
  border-radius: 8px 8px 0 0;
}
.fourth_block_height {
  display: flex;
  flex-direction: column;
  padding: 30px;
  background-color: #F5F5FA;
}
.fourth_block_height_container {
  display: flex;
  align-items: center;
}
.fourth_block_height_container_first {
  font-size: 14px;
  font-weight: 700;
  line-height: 17.07px;
  color: #24A3FF;
  padding-bottom: 5px;
}
.fourth_block_height_container_second {
  font-size: 38px;
  font-weight: 700;
  line-height: 46.32px;
  background: -webkit-linear-gradient(90.13deg, #F0145A 0.84%, #EF8080 99.96%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  padding-bottom: 30px;
}
.fourth_block_height_container_third {
  font-size: 16px;
  font-weight: 700;
  line-height: 19.5px;
  color: #B3B3BA;
  padding-bottom: 6px;
}
.fourth_block_height_container_fourth {
  font-size: 14px;
  font-weight: 400;
  line-height: 22px;
  color: #B3B3BA;
}

.fifth {
  padding-bottom: 60px;
}
.fifth_block_header-text {
  display: flex;
  justify-content: center;
  padding-bottom: 30px;
  font-size: 14px;
  font-weight: 700;
  line-height: 17.07px;
  color: #323264;
}
.fifth_block_header_img {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 0;
  margin: 0 auto;
  gap: 0;
  font-size: 0;
}
.fifth_block_header_img_component {
  display: block;
  margin: 0;
  padding: 0;
}

.sixth {
  background-color: #F5F5FA;
}
.sixth_block {
  display: flex;
  padding: 0 160px;
  flex-direction: column;
}
.sixth_block_heading {
  padding-top: 30px;
}
.sixth_block_heading_block {
  display: flex;
  align-items: center;
  gap: 2px;
  padding-bottom: 10px;
}
.sixth_block_heading_block_up-text {
  font-size: 14px;
  font-weight: 700;
  line-height: 17.07px;
  color: #24A3FF;
}
.sixth_block_heading_low-text {
  font-size: 38px;
  font-weight: 700;
  line-height: 46.32px;
  background: -webkit-linear-gradient(90.13deg, #F0145A 0.84%, #EF8080 99.96%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.sixth_block_container {
  display: flex;
  gap: 30px;
  padding-top: 30px;
}
.sixth_block_container_card {
  max-width: 514px;
  background-color: white;
}
.sixth_block_container_card_img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 8px 8px 0 0;
}
.sixth_block_container_card_text-container {
  display: flex;
  flex-direction: column;
  gap: 15px;
  max-width: 514px;
  padding: 30px;
}
.sixth_block_container_card_text-first {
  font-size: 16px;
  font-weight: 700;
  line-height: 19.5px;
  color: #24A3FF;
  padding-bottom: 15px;
}
.sixth_block_container_card_text-second {
  font-size: 12px;
  font-weight: 700;
  line-height: 14.63px;
  color: #B3B3BA;
  padding-bottom: 15px;
}
.sixth_block_container_card_horizontal-line {
  width: 100%;
  height: 4px;
  background: #E6E6F0;
}
.sixth_block_container_card_text-third {
  font-size: 14px;
  font-weight: 400;
  line-height: 22px;
  color: #000000;
}

.footer {
  padding: 0 160px;
  background-color: #F5F5FA;
  padding-top: 30px;
}
.footer_horizontal-line {
  display: flex;
  justify-content: center;
  margin: 0 auto;
  min-height: 2px;
  background: #E6E6F0;
}
.footer_block {
  display: flex;
  justify-content: space-between;
  padding-top: 30px;
}
.footer_block_left-block_upper {
  display: flex;
  align-items: center;
  gap: 10px;
  padding-bottom: 21px;
}
.footer_block_left-block_upper_text {
  font-size: 16px;
  font-weight: 400;
  line-height: 19.5px;
  color: #323264;
}
.footer_block_left-block_upper_dot {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background-color: #B3B3BA;
}
.footer_block_left-block_upper_on-map {
  text-decoration: none;
  font-size: 12px;
  font-weight: 700;
  line-height: 14.63px;
  color: #24A3FF;
}
.footer_block_left-block_lower {
  display: flex;
  align-items: center;
  gap: 10px;
  padding-bottom: 30px;
}
.footer_block_left-block_lower_facebook-icon {
  color: #24A3FF;
}
.footer_block_left-block_lower_dot {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background-color: #B3B3BA;
}
.footer_block_left-block_lower_number {
  font-size: 16px;
  font-weight: 700;
  line-height: 19.5px;
  color: #46466E;
}
.footer_block_left-block_lower_email {
  text-decoration: none;
  font-size: 16px;
  font-weight: 700;
  line-height: 19.5px;
  color: #24A3FF;
}
.footer_block_right-block_text {
  font-size: 16px;
  font-weight: 400;
  line-height: 19.5px;
  color: #B3B3BA;
}

.footer_block_left-block_lower_facebook-icon:hover {
  color: #F0145A;
  transition: all 0.5s ease;
}

.footer_block_left-block_lower_email:hover {
  color: #F0145A;
  transition: all 0.2s ease;
}

@media (max-width: 1440px) {
