  .first-block_center {
    display: flex;
    justify-content: center; /* Центрирование по горизонтали */
    align-items: center; /* Центрирование по вертикали */
    height: 100vh; /* 100% высоты экрана */
    padding: 0 20px; /* Небольшие отступы, чтобы не прилипало к краям */
    box-sizing: border-box;
  }
  .third-header {
    display: block;
  }
  .third-header_upper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 15px;
    background: rgba(245, 245, 250, 0.2);
    min-height: 45px;
  }
  .third-header_upper_language {
    display: flex;
    align-items: center;
    gap: 6px;
  }
  .third-header_upper_language_text {
    font-size: 12px;
    font-weight: 700;
    line-height: 14.63px;
    text-decoration: none;
    color: #FFFFFF;
  }
  .third-header_upper_language_text-active {
    font-size: 12px;
    font-weight: 700;
    line-height: 14.63px;
    color: #FFFFFF;
    text-decoration: none;
    background: #F0145A;
    padding: 5px 7px;
    border-radius: 50%;
  }
  .third-header_upper_text-number {
    font-size: 16px;
    font-weight: 700;
    line-height: 19.5px;
    color: #FFFFFF;
  }
  .third-header_block {
    display: flex;
    align-items: center;
    padding: 15px;
  }
  .third-header_block_logo {
    gap: 6px;
    display: flex;
    align-items: center;
  }
  .third-header_block_logo_svg {
    width: 69.33px;
    height: 32px;
  }
  .third-header_block_logo_text {
    width: 11.62px;
    height: 26px;
    gap: 0px;
    opacity: 0px;
    color: #F0145A;
  }
  .third-header_block_logo_text-second {
    color: #FFFFFF;
    font-size: 20px;
    font-weight: 400;
    line-height: 24.38px;
  }
  .third-header_block_logo_humburger {
    display: flex;
    margin-left: auto;
  }
  .second-header {
    display: none;
  }
  .third-block_card-container {
    grid-template: auto auto/auto;
  }
  .third-block_card-container_card {
    max-width: 294px;
  }
  .third-block_height-text {
    padding: 0 60px;
  }
  .fifth_block_header_img_component:nth-child(n+6) {
    display: none;
  }
  .fifth_block_header_img_component {
    max-width: 375px;
  }
  .fourth_block {
    flex-direction: column;
  }
  .fourth_block_img {
    min-width: 315px;
    min-height: 500px;
    -o-object-fit: cover;
    object-fit: cover;
    border-radius: 8px 8px 0 0;
  }
  .sixth_block_container {
    justify-content: center;
  }
  .sixth_block_container {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  .footer_horizontal-line {
    max-width: 315px;
  }
  .footer_block {
    flex-direction: column;
  }
  .footer_block_left-block {
    display: flex;
    flex-direction: column;
    padding-bottom: 30px;
  }
  .footer_block_left-block_upper {
    display: flex;
    flex-direction: column;
  }
  .footer_block_left-block_upper_dot {
    display: none;
  }
  .footer_block_left-block_lower {
    display: flex;
    flex-direction: column;
  }
  .footer_block_left-block_lower_dot {
    display: none;
  }
  .footer_block_right-block {
    display: flex;
    justify-content: flex-start;
    min-width: 315px;
  }
}
.first-block_header_right-menu_lower_text:hover {
  color: #FFFFFF;
  border-bottom: solid;
}